import React, {useContext} from "react"
import {cartContext} from "../Global/cartContext"
import {Link} from "react-router-dom"

const Navbar = () => {
   const {shoppingCart, qty} = useContext(cartContext);
    return(
        <nav>
            <ul className="left">
            <li><Link to="/react-ecommerce">MT-Store</Link></li>
            </ul>
            <ul className="right">
                <li><Link to="/react-ecommerce/cart"><span><i className="fas fa-cart-plus"></i></span><span className="shoppingCartTotal">{qty}</span></Link></li>
            </ul>
        </nav>
    )

}

export default Navbar;